.about {
  height: 100vh;
  display: flex;
  align-items: center;
  background-color: rgb(205, 255, 238);
}

.about--left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.about--right {
  flex: 1;
  display: flex;
  justify-content:center;
  flex-direction: column;
  align-items: center;
  padding-right: 2px;
}

.about--card {
  width: 100%;
  height: 70vh;
}

.about--picture {
  padding-top: 3vh;
  width: 80%;
  height: auto;
  object-fit: cover;
  margin-left: 0;
  padding-left: 30px;
}

.about--right--title {
  font-size: 50px;
}

.about--right--text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: left;
  padding-left: 0;
}

.about--right--presentation {
  font-size: 3vh;
  font-weight: 400;
}

@media screen and (max-width: 480px) {
  .about--left {
    display: none;
  }

  .about {
    height: 100vh;
    display: flex;
    align-items: center;
    background-color: rgb(205, 255, 238);
    padding: 40px;
  }
}